html , body{
  min-height: 100% ;
}


.dark-theme {
	--c-first: #303030;
	--c--first: #ffffff;
	--c-second: #a2d2ff;
	--c-third: #fa8d8d;
	--c-forth: #ffc581;
	--c-fifth: #74e092;
	--c-sixth: #d7dde5;
}

.light-theme {
	--c-first: #ffffff;
	--c--first: #303030;
	--c-second: #1272cc;
	--c-third: #ba2323;
	--c-forth: #c76b00;
	--c-fifth: #00952a;
	--c-sixth: #303030;
}




body {
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

  margin: 0;
	padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

	background-color: var(--c-first) !important;
	color: var(--clr-font);
	line-height: 1.5;
	font-size: 0.875rem;
	transition: all 0.3s linear;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul,
ol {
	list-style: none;
  margin: 0;
  padding: 0;
}



.App{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100% ;
}


header {
	width: fit-content;
  color: #ee9ca7;
}


.nav-class {
	background-color: var(--c-second);
	color: var(--c-first);
  box-shadow: 0 0 3px 1px var(--c--first);
	/* border-bottom: 1px solid var(--c--first); */
}

.header-class h2 {
	color: var(--c--first);
}

.header-class p {
	color: var(--c-sixth);
}

.logo-class {
	font-size: 150px;
	color: var(--c-second);
}

.article-class h4 {
	color: var(--c-third);
}

.article-class p {
	color: var(--c-sixth);
}

.btn-mode {
	background-color: transparent;
	color: inherit;
	border: none;
	cursor: pointer;
}
